import React from 'react'
import Nav from '../components/common/nav'
import Footer from '../components/common/footer'
import Head from 'next/head'
import {Collapse} from 'antd'
const {Panel} = Collapse;
export default () => {
    return (
        <div className="bg-gray-100" style={{backgroundImage: "url('/static/img/bg.svg')"}}>
            <Head>
                <title>CryptoFair - The easiest and fastest way to make money in crypto industry</title>
            </Head>
            <div className="bg-white">
                <div className="container mx-auto px-4">
                    <Nav />
                </div>
            </div>
            <div className="container mx-auto px-4 py-32">
                <div className="">
                    <p className="text-4xl text-cryp-text font-bold text-center mb-8">FAQ</p>
                </div>
            </div>
            <Footer />
        </div>
    )
}